import { useLocation, useNavigate, useParams } from "react-router-dom";
import { DataContext } from "../../components/RestTable";
import { useEffect, useState } from "react";
import { api } from "../../App";

export default function SectionsUpsert(props) {

    let { id } = useParams();
    const location = useLocation();
    const navigate = useNavigate();
    const STATE = { UPDATE: "Update", CREATE: "Create" }
    const state = id ? STATE.UPDATE : STATE.CREATE;
    const data = DataContext._currentValue.data;
    const item = data && data.find(item => item.id === parseInt(id));

    const [isLoading, setIsLoading] = useState(false);
    const [status, setStatus] = useState('');

    useEffect(() => {
        setStatus('');
        setIsLoading(false);

        if (item) {
            Object.keys(item).forEach(key => {
                document.getElementsByName(key)[0].value = item[key];
            })
        }
        else {
            document.querySelectorAll('input').forEach(input => {
                if (input.name === 'id')
                    input.value = 0;
                else
                    input.value = '';
            })
        }
    }, [location.pathname]);

    function handleSubmit(e) {
        e.preventDefault();
        setIsLoading(true);

        api.post('/' + props.title + '/upsert', {
            ...Object.fromEntries(new FormData(e.target))
        }).then(res => {
            if (res.status.toString().startsWith('2')) {
                setStatus('Success');
                navigate('/admin/' + props.title);
            }
        }).catch(err => {
            setStatus('Failed');
        }).finally(() => {
            setIsLoading(false);
        });
    }

    return (
        <div className="w-full flex justify-between">
            <div className="my-2 mx-5 w-full">
                <h1 className="text-warning">
                    {state} {status}
                </h1>
                <form onSubmit={handleSubmit}>
                    <div className="w-full grid lg:grid-cols-2 lg:gap-x-4">
                        {
                            props.attributes && Object.keys(props.attributes).map((key, index) => {
                                if (key === 'id') {
                                    return (
                                        <input name={key} type="hidden" key={key} />
                                    )
                                }
                                return (
                                    <div className="form-control w-full" key={key}>
                                        <label className="label label-text">{key}</label>
                                        <input name={key} type={props.attributes[key]} placeholder={key} className="input responsive-input input-bordered w-full" />
                                    </div>
                                )
                            })
                        }
                    </div>
                    <br />
                    <div className="form-control w-full">
                        <button disabled={isLoading} className="btn btn-block btn-primary">{isLoading ? <div className="loading loading-dots"></div> : state}</button>
                    </div>
                </form>
            </div>
        </div>
    );
}