import { createContext, useEffect, useState } from "react";
import { Link, Outlet, useLocation } from "react-router-dom";
import { api } from "../App";

export const DataContext = createContext(null);

export default function RestTable(props) {

    const [data, setData] = useState(null)
    const [reload, setReload] = useState(false)

    const location = useLocation();

    useEffect(() => {
        if (location.pathname.includes('upsert')) {
            return;
        }
        api.get('/' + props.title)
            .then(res => {
                if (res.status === 200) {
                    let data = res.data;

                    if (data.length > 0) {
                        if (props.ignore) {
                            props.ignore.forEach(element => {
                                data = data.map((item) => {
                                    delete item[element]
                                    return item
                                })
                            });
                        }
                        // sort data by order
                        data = data.sort((a, b) => {
                            return a.order - b.order;
                        })
                        setData(data);
                    }
                }
            }).catch(err => {
                // console.log(err);
            });
    }, [location.pathname, reload]);

    return (
        <>
            <div className="w-full flex justify-between">
                <h1 className="text-3xl text-primary my-2 ms-5">
                    {props.title}
                </h1>
                <Link to={"/admin/" + props.title + "/upsert"}>
                    <button className="btn btn-primary btn-sm me-5 my-2">New</button>
                </Link>
            </div>
            <DataContext.Provider value={{ data: data }}>
                <Outlet />
            </DataContext.Provider>
            <div className="overflow-x-auto w-[28rem] sm:w-full">
                <div className="table-container">
                    <table className="table table-hover">
                        <thead>
                            <tr>
                                {
                                    data && Object.keys(data[0]).map((key, index) => {
                                        return (
                                            <th key={index}>
                                                {key}
                                            </th>
                                        )
                                    })
                                }
                                <th>
                                    Actions
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                data && data.map((item, index) => {
                                    return (
                                        <tr key={index}>
                                            {
                                                Object.values(item).map((value, index) => {
                                                    return (
                                                        <td key={index}>
                                                            {
                                                                typeof value === 'object' ? value.title : value
                                                            }
                                                        </td>
                                                    )
                                                })
                                            }
                                            <td>
                                                <Link to={"/admin/" + props.title + "/upsert/" + item.id}>
                                                    <button className="link link-secondary">Edit</button>
                                                </Link>
                                                &nbsp;
                                                <button onClick={e => {
                                                    const confirm = window.confirm('Are you sure you want to delete this item?');
                                                    if (confirm) {
                                                        api.delete('/' + props.title + '/' + item.id)
                                                            .then(res => {
                                                                if (res.status.toString().startsWith('2')) {
                                                                    setReload(!reload);
                                                                }
                                                            }).catch(err => {
                                                                // console.log(err);
                                                            });
                                                    }
                                                }} className="link link-error">Delete</button>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>

            </div>
            <br />
        </>
    );
}